nav {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #ffffff50;
  @include media("<=768px") {
    background-color: #b4c6ee;
    position: fixed;
    top: 0;
  }
  .logo {
    cursor: pointer;
  }

  .nav-menu {
    margin-bottom: 0px !important;
    @include media("<=760px") {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 70px;
      left: -100%;
      opacity: 0;
      transition: all 0.5s ease;
      justify-self: center;
    }
    li {
      list-style: none;
      transition: all 0.5s ease;
      @include media("<=760px") {
        text-align: center;
        padding: 2rem;
        width: 100%;
        margin: 0 !important;
      }
    }
  }
  .nav-menu.active {
    top: 65px;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    padding: 0 !important;
    background-color: #b4c6ee;
    li {
      .nav-links {
        text-decoration: none;
      }
      &:hover .nav-links {
      }
      &:hover {
        cursor: pointer;
        background-color: #fff;
      }
    }
  }

  .connect {
    .button {
      width: 150px;
      border-radius: 30px;
      @include media("<=760px") {
        display: none;
      }
    }
  }
  .menu-icon {
    display: none;
    @include media("<=760px") {
      display: block;
      cursor: pointer;
      width: 30px;
    }
  }
}
