.package__item {
  border-radius: 15px;
  transition: all 0.5s ease;
  height: 600px;
  &:hover {
    transform: translateY(-10px);
    cursor: pointer;
    box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.45) !important;
  }
  .my__card {
    height: 600px;

    img {
      border-radius: 15px;
    }
    .text {
      .header {
        font-weight: 800;
      }
      .sub {
        font-size: 1rem;
        font-weight: 400;
      }
    }
    .card__text {
      .icon-container {
        .icons {
          i {
            border-radius: 50%;
            background-color: $primary-blue;
            padding: 10px;
            color: $primary-white;
          }
        }
      }
    }
  }
}
