.badge__item {
  text-align: left;
  transition: all 0.5s ease;
  height: 270px;
  border-radius: 20px;

  &:hover {
    cursor: pointer;
    transform: translateY(-10px);
  }

  .icon {
    i {
      color: $primary-blue;
      font-size: 50px;
      background-color: $secondary-blue;
      padding: 20px 30px;
      border-radius: 20px;
    }
  }
  .badge__header {
    font-weight: 800;
  }
  .badge__subscription {
    font-size: 1rem;
    font-weight: 400;
  }
}
