@import "./variables.scss";
@import "./breakpoint.scss";

@import "./components/navbar.scss";
@import "./components/herosection.scss";
@import "./components/badge.scss";
@import "./components/badgeitem.scss";
@import "./components/package.scss";
@import "./components/packageitem.scss";
@import "./components/blog.scss";
@import "./components/blogitem.scss";
@import "./components/feedbackitem.scss";
@import "./components/footer.scss";
* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  color: $primary-dark !important;
  font-size: 1.3rem !important;
}
ul {
  list-style: none;
  li {
    a {
      text-decoration: none;
      color: $primary-dark;
      opacity: 0.75;
      padding-bottom: 5px;
      &:hover {
        color: $primary-dark;
        opacity: 1;
      }
    }
    a.active {
      opacity: 1;
      border-bottom: 3px solid $primary-dark;
    }
  }
}
