section.hero-section {
  background-image: url("./../../assets/bg-img.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: 2;
  position: relative;
  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #00000028;
  }
  .row {
    height: 100%;
    align-items: center;
    justify-content: center;
    .hero {
      z-index: 5;
      .hero__text {
        z-index: 5;
        .header__text {
          font-size: 5rem;
          font-weight: 700;
          color: $primary-white;
          @include media("<=desktop") {
            font-size: 4rem;
          }
          @include media("<=768px") {
            font-size: 3rem;
          }
        }
        .subheader__text {
          text-transform: uppercase;
          color: $primary-white;
          font-size: 1.3rem;
          font-weight: 500;
          @include media("<=desktop") {
            font-size: 1rem;
          }
          @include media("<=768px") {
            font-size: 1rem;
          }
        }
      }
      .booking {
        z-index: 5;
        .row {
          background-color: #fff;
          padding: 20px 0;
          border-radius: 10px;
          .col-2 {
            border-left: 1px solid #b4c6ee;
            border-right: 1px solid #b4c6ee;
          }
          h4 {
            font-size: 1.4rem;
            font-weight: 800;
          }
          p {
            color: #7e7e7e;
          }
          button {
            font-weight: 800;
          }
        }
      }
    }
  }
}
