.blog__item {
  transition: all 0.5s ease;
  &:hover {
    cursor: pointer;
  }
  &:hover h3 {
    transform: scale(1.05);
  }
  .blog__card {
    width: 100%;
    height: 400px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 15px;
    position: relative;
    z-index: -1;

    &::after {
      content: "";
      position: absolute;
      background: rgba(0, 0, 0, 0.2) !important;
      border-radius: 15px;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;

      &:hover {
        cursor: pointer;
        color: $primary-dark;
      }
    }
    .blog__text {
      color: $primary-white;
      text-align: center;
      z-index: 5;

      h3 {
        font-weight: 900;
        transition: all 0.5s ease;
      }
    }
  }
}
